* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.link {
  color: inherit;
  text-decoration: none;
}

.main-header {
  background-color: var(--color-dark);
  background-image: url("background-lines-mobile.22d82665.png");
  background-image: url("background-lines-mobile.22d82665.png"), linear-gradient(var(--color-transparent), var(--color-transparent));
  background-position: center;
  background-size: 100%;
}

.main-header-container {
  color: var(--color-white);
  max-width: 320px;
  height: 230px;
  margin: 0 auto;
  padding: 40px 20px 44px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

.first-heading {
  display: none;
}

.main-header-list {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 44px;
  font-weight: 500;
  list-style: none;
  display: flex;
}

.main-header-list-left {
  align-items: center;
  gap: 8px;
  display: flex;
}

.logo-link {
  transition: transform .25s;
  display: inline-block;
}

.logo-link:hover, .logo-link:focus {
  transform: scale(1.1);
}

.icon-film {
  max-width: 24px;
  max-height: 24px;
}

.logo-text {
  font-size: 30px;
  display: none;
}

.main-header-list-right {
  text-transform: uppercase;
  gap: 39px;
  font-size: 12px;
  display: flex;
}

.main-header-li {
  list-style: none;
  position: relative;
}

.link:focus {
  outline: none;
}

.main-header-list-right .link:after {
  background-color: var(--color-red);
  content: "";
  width: 0;
  height: 3px;
  transition: width .25s ease-in-out;
  position: absolute;
  bottom: -4px;
  left: 0;
}

.main-header-list-right .link:hover:after, .main-header-list-right .link:focus:after, .main-header-list-right .main-header-li:first-child .link:after {
  width: 100%;
}

.main-header-form {
  margin-bottom: 16px;
  position: relative;
}

.main-header-input {
  border: none;
  border-bottom: 1px solid var(--color-white);
  color: var(--color-white);
  background: none;
  width: 100%;
  padding: 4px;
  transition: box-shadow .25s;
}

.main-header-input::placeholder {
  opacity: 1;
  color: var(--color-white);
}

.main-header-input:focus {
  box-shadow: 0 2px var(--color-white);
  outline: none;
}

.main-header-submit {
  cursor: pointer;
  background: none;
  border: none;
  width: 20px;
  height: 20px;
  padding: 4px;
  position: absolute;
  bottom: 5px;
  right: -4px;
}

.icon-search {
  width: 100%;
  height: 100%;
  transition: transform .25s;
}

.main-header-submit:hover, .main-header-submit:focus {
  outline: none;
}

.main-header-submit:hover .icon-search, .main-header-submit:focus .icon-search {
  transform: scale(1.3);
}

.main-header-fail {
  color: var(--color-red);
  opacity: 0;
}

